import Lines from '@/components/comment/Lines.vue'
import CommodityList from '@/components/comment/CommodityList.vue'
import  http from '@/comment/api/request.js'
export default{
	components:{
		Lines,
		CommodityList
	},
	props:{
		keywords:String
	},
	data(){
		return{
			topList:{
				currendIndex:0,
				data:[
					{name:"价格",status:1},
					{name:"折扣",status:0},
					{name:"品牌",status:0}]
				
			},
			dataList:[]
		}
	},
	//注意：这不是页面，是组件，所以要在mounted里面写请求
	mounted(){
		//请求数据，我们根据search-list传过来的keywords来请求
		
			http.request({
				url:'/goods/search',
				data:{
					name:this.keywords,
					pprice:"desc"
				}
			})
			 .then(res=>{
				 console.log(res)
			 })
			 .catch(()=>{
				 console.log("请求失败")
			 })
			
			
		
	},
	methods:{
		//请求搜索数据的方法
		getData(){
			http.request({
				url:'/goods/search',
				data:{
					name:this.keywords,
					pprice:"desc"
				}
			})
			 .then(res=>{
				 console.log(res)
			 })
			 .catch(()=>{
				 console.log("请求失败")
			 })
			
			
		},
		tapChange(i){
			//定义初始状态，刚开始渲染的时候
			let idx=this.topList.currendIndex//初始为0
			let item=this.topList.data[idx]
			if(idx==i){
				return item.status=item.status===1 ? 2: 1
		}
		let newItem=this.topList.data[i]
		item.status=0;
		this.topList.currendIndex=i;
		newItem.status=1	
	}
},
}
